.skill-menu {
    width: 31.2rem;
    position: fixed;
    display: flex;
    flex-direction: column;
    top: 30%;
    right: 35vw;
    align-items: flex-end;
  }
  
  .skill-item {
    height: 3.7rem;
    width: 59%;
    display: flex;
    position: relative;
    justify-content: flex-end;
    align-items: center;
    cursor: pointer;
  }

  
  .skill-title {
    font-size: 1rem;
    padding-right: 4.5rem;
  }
  
  .skill-item.activeSkill .skill-title {
    font-size: 1.8rem;
  }
  
  .activeSkill {
    border-radius: var(--radius-left);
    cursor: default;
  }
  
  .skill-item.activeSkill:nth-child(1) {
    background: linear-gradient(270deg, #ffffff00, var(--purple) 70%);
  }
  .skill-item.activeSkill:nth-child(2) {
    background: linear-gradient(270deg, #ffffff00, var(--blue) 70%);
  }
  
  .skill-icon {
    width: 4rem;
    height: 4rem;
    position: absolute;
    top: 1.8rem;
    left: 6rem;
  }
  
  .skill-sub-container-1 h3,
  .skill-sub-container-2 h3 {
    position: relative;
    margin: 5px;
  }
  
  .menu {
    width: 19rem;
    position: fixed;
    display: flex;
    flex-direction: column;
    top: 50%;
    transform: translateY(-25%);
    right: 15vw;
  }
  
  .skill-sub-container-1,
  .skill-sub-container-2 {
    display: flex;
    justify-content: right;
    gap: 2rem;
  }
  
  .level-container {
    display: flex;
    gap: 0.3rem;
  }
  
  .level-point {
    width: 3rem;
    height: 2rem;
    border-radius: 0.2rem;
  }
  
  .filled {
    background: linear-gradient(180deg, #236473, #25afce, #7ce7ff);
  }
  .unfilled {
    background: linear-gradient(180deg, #1c8298, #074654, #074654);
  }
  
  .skill-sub-container {
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
  }
  
  @media (max-width: 1100px) {
    .skill-item.activeSkill .skill-title {
      font-size:1.2rem;
    }
    .skill-icon {
      display: none;
    }
    .level-point {
      width: 1.5rem;
      height: 1rem;
      border-radius: 0.1rem;
    }
    .level-container {
      gap: 0.15rem;
    }
    .skill-sub-container-1,
    .skill-sub-container-2 {
      gap: 0.5rem;
    }
    .skill-sub-container {
      margin-top: 2rem;
      gap: 0.6rem;
    }
    h1 {
      font-size: 0.8rem;
    }
    h2,
    h3,
    p {
      font-size: 0.7rem;
    }
    .skill-menu {
      flex-direction: column;
      left: 50%;
      transform: translateX(-50%);
      align-items: center;
      top: 30%;
    }
    .skill-item {
      height: 2rem;
      width: 12em;
    }
  }
  @media (max-width: 700px) {
    .menu {
      width: 10rem;
      top: 30%;
      left: 50%;
      transform: translateY(-50%);
    }
  }

  