#bg {
    object-fit: cover;
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: -5;
  }
  
  .shadow-overlay {
    position: fixed;
    top: 0;
    left: 0;
    z-index: -4;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.718);
  }
  
  @media (max-width: 700px) {
    .shadow-overlay {
      background-color: rgba(0, 0, 0, 0.7);
    }
  }